.cw-header__number-one {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	color: white;
	background-color: var(--primaryBlue2);
	padding: 0.5rem;
	gap: 0.75rem;
	/* font-size: 0.75rem; */
}
.cw-header__number-one strong {
	font-weight: 800;
}
.cw-header__number-one p {
	margin: 0;
	padding: 0;
	font-size: 12px;
}
.cw-header__flag {
	flex: 0 0 24px;
	width: 24px;
	height: 24px;
	object-fit: contain;
}

@media (min-width: 1024px) {
	.cw-header__number-one {
		/* font-size: 1rem; */
		font-size: 16px;
		padding: 0.7rem;
	}

	.cw-header__number-one p {
		font-size: 16px;
	}

	.cw-header__flag {
		flex: 0 0 30px;
		width: 30px;
		height: 30px;
	}
}
